div.characterSelectAnchor {
  position: relative;
  left: 50%;
  width: 792px;
  padding-top: 25px;
}

div.characterSelectContainer {
  display: grid;
  grid-template: repeat(5, 132px) / 1fr;
  position: relative;
  left: -100%;
  width: 1584px;
  height: 660px;
}



