.matchupAnchor {
  position: relative;
  left: 50%;
  width: 402px;
  padding-top: 25px;
}

.matchup {
  position: relative;
  left: -50%;
  width: 396px;
  display: grid;
  grid-template: 132px 30px / repeat(3, 132px);
  border: red;
  border-style: solid;
}

.vsFlairContainer {
  position: relative;
}

.vsFlair {
  background-image: url(../../Images/Vs.png);
  width: 128px;
  height: 128px;
}



